.card {
  background: #fff;
  border-radius: 2px;
  height: 325px;
  margin: 1rem;
  position: relative;
  width: 325px;
  -webkit-box-shadow: 0 3px 6px #999, 0 3px 6px #999;
          box-shadow: 0 3px 6px #999, 0 3px 6px #999;
  text-align: left;
}

.card > .img-container {
  height: 100%;
  overflow: hidden;
  text-align: center;
  background: white;
}
.card > .img-container > img {
  width: 60%
}

h2 {
	color: #232323;
	margin-left: 2.5%;
}

.message {
	color: red;
}

.pics {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: row;
	    flex-direction: row;
	-ms-flex-pack: start;
	    justify-content: flex-start;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex-wrap: wrap;
	    flex-wrap: wrap;
}

.pics > * {
	width: 20%;
	margin-right: 2.5%;
	margin-left: 2.5%;
}

.pic > img {
	width: 100%;
	height: auto;
}


